<script>
	import { onMount } from 'svelte';
	import Counter from './Counter.svelte';
	let ws;
	let data;
	import ReconnectingWebSocket from './reconnecting-websocket.js';
	onMount(() => {
		let initdata = [
			"miss",
			"sliderBreaks",
			"ppIfMapEndsNow",
			"ppIfRestFced",
			"osu_mSSPP",
			"osu_m99PP",
			"osu_m98PP",
			"osu_m95PP",
			"osu_m90PP",
			"status"
		]
		ws = new ReconnectingWebSocket("ws://localhost:28390/tokens", null, {debug: true, reconnectInterval: 3000});
		ws.onopen = function (event) {
			console.log("Connected to websocket.")
			ws.send(JSON.stringify(initdata)); // send init data.
		}
		ws.onmessage = function (event) {
			data = {...data, ...JSON.parse(event.data)}; // merge data.
			if (data != null){
				if (data.status == 1){
					data.ppIfMapEndsNow = 0;
					data.ppIfRestFced = 0;
					data.miss = 0;
					data.sliderBreaks = 0;
				}
			}
		}
	});
</script>



<main>
	{#if data != null && data.osu_m90PP != null}
		<div class="ppcontainer {data.status != 1 ? '' : 'up'}">
			<div class="ppbox" style="text-shadow: none"><Counter value={data.ppIfMapEndsNow} multiplier=25></Counter><div class="text pptext" style="text-shadow: none">PP</div></div>
		</div>
		<div class="ppcontainer" style="margin-top: 30px; display: block;">
			<div class="textobj {data.miss > 0 || data.sliderBreaks > 0 ? 'nothidden' : 'hidden'}">FC: <Counter value={data.ppIfRestFced} multiplier=5></Counter></div>
			<div class="textobj {data.sliderBreaks > 0 ? 'nothidden' : 'hidden'}">SliderBreaks: {data.sliderBreaks}x</div>
			<div class="textobj {data.miss > 0 ? 'nothidden' : 'hidden'}">Miss: {data.miss}x</div>
		</div>
		<div class="listening {data.status == 1 ? '' : 'hid'}">
			<div class="accbadge">90%</div><div class="ppbadge"><Counter value={data.osu_m90PP}></Counter><div class="text pptext">pp</div></div>
			<div class="accbadge">95%</div><div class="ppbadge"><Counter value={data.osu_m95PP}></Counter><div class="text pptext">pp</div></div>
			<div class="accbadge">98%</div><div class="ppbadge"><Counter value={data.osu_m98PP}></Counter><div class="text pptext">pp</div></div>
			<div class="accbadge">99%</div><div class="ppbadge"><Counter value={data.osu_m99PP}></Counter><div class="text pptext">pp</div></div>
			<div class="accbadge">100%</div><div class="ppbadge"><Counter value={data.osu_mSSPP}></Counter><div class="text pptext">pp</div></div>
		</div>

	{/if}
</main>

<style>
.up {
	transform: translateY(-80px) !important;
}
.ppcontainer {
	transform: translateY(0);
}
:global(*) {
	transition: all 0.5s, color 0s, background-color 0s;
	opacity: 1;
}
.hidden { opacity: 0; height: 0px }
.nothidden { opacity: 1; height: 25px }
.hid { 
	opacity: 0 !important;
}
.ppbox {
	padding: 10px 20px 15px 20px;
	border-radius: 0px 0px 25px 25px;
	font-size: 40px;
	background-color: #343334;
	border: 5px solid;
	border-color: #181718;
	border-top-width: 0;
}
.ppcontainer {
	display: flex;
	justify-content: center;
	font-size: 20px;
	text-align: center;
}
.item {
	padding: 10px;
	border-radius: 25px;
	border: 5px solid;
}
.ppitem {
	background-color: #343434;
	border-color: #595959;
}
.missitem {
	background-color: #600807;
	border-color: #7f0b0a;
}

@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@600&display=swap');
*{
	font-family: 'Quicksand', sans-serif;
}
:global(body) { 
	background-color: white;
	margin: 0;
	padding: 0;
	height: 1000px;
	color: rgba(255, 255, 255, 0.8);
	text-shadow: 0px 0px 10px rgba(255, 255, 255, 0.6), 0px 0px 46px rgba(255, 255, 255, 0.6), 0px 0px 80px rgba(255, 255, 255, 0.6);
}
.listening {
	display: flex;
	justify-content: center;
	font-size: 20px;
	text-align: center;
	margin-top: 30px;
	opacity: 1;
}
.accbadge {
	background-color: rgba(0, 0, 0, 0.8);
	padding: 8px 16px 8px 16px;
	font-size: 20px;
}
.ppbadge {
	background-color: rgba(0, 0, 0, 0.6);
	padding: 8px 16px 8px 16px;
	font-size: 20px;
	margin-right: 20px;
	z-index: 1;
}
.text {
	display: inline-flex;
	margin-left: 4px;
}
.pptext {
	color: rgb(50, 200, 255);
	text-shadow: 0px 0px 10px rgba(50, 200, 255, 1), 0px 0px 46px rgba(50, 200, 255, 1), 0px 0px 80px rgba(50, 200, 255, 1);
}
.misstext {
	color: rgb(255, 50, 50);
	text-shadow: 0px 0px 10px rgba(255, 50, 50, 1), 0px 0px 46px rgba(255, 50, 50, 1), 0px 0px 80px rgba(255, 50, 50, 1);
}
